import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _forEach from 'lodash/forEach';
import _get from 'lodash/get';
import _map from 'lodash/map';

import BoardSquare from './BoardSquare.react';

import './board.css';


class Board extends Component {
  static propTypes = {
    initialStones: PropTypes.array,
    boardSize: PropTypes.number,

  };

  static defaultProps = {
    boardSize: 19,
  };

  state = {
    board: [[]],
  };

  componentDidMount() {
    const { boardSize, initialStones } = this.props;
    var board = new Array(boardSize);
    for (var i = 0; i < boardSize; i++) {
      board[i] = new Array(boardSize);
    }

    let minX = boardSize;
    let minY = boardSize;
    let maxX = -1;
    let maxY = -1;

    _forEach(initialStones, (stone) => {
      const { x, y } = stone;
      board[y][x] = stone;
      minX = Math.min(minX, x);
      minY = Math.min(minY, y);
      maxX = Math.max(maxX, x);
      maxY = Math.max(maxY, y);
    });

    this.setState({board: board})
  }

  render() {
    const { boardSize } = this.props;
    const { board } = this.state;
    const boardSquares = _map(board, (row, rowIdx) => {
      const rowSquares = _map(row, (square, colIdx) => {
        const color = _get(square, 'color')
        return (
          <BoardSquare
            key={`${colIdx}_${rowIdx}`}
            boardSize={boardSize}
            x={rowIdx}
            y={colIdx}
            color={color}
            nextMoveColor={2}
          />
        );
      });
      return (
        <div className="board-row" key={rowIdx}>
          {rowSquares}
        </div>
      );
    });


    return (
      <div className="board">
        {boardSquares}
      </div>
    );
  }
}

export default Board;



