import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

// eslint-disable-next-line
import corner from './corner.png';
// eslint-disable-next-line
import middle from './middle.png';
// eslint-disable-next-line
import side from './side.png'

class BoardSquare extends Component {
  static propTypes = {
    boardSize: PropTypes.number.required,
    x: PropTypes.number.required,
    y: PropTypes.number.required,
    color: PropTypes.number.required,
    nextMoveColor: PropTypes.number.required,
  };

  render() {
    const { boardSize, x, y, color, nextMoveColor } = this.props;
    const squareClassNames = classnames('square', {
      firstrow: x === 0,
      lastrow: x === boardSize-1,
      firstcol: y === 0,
      lastcol: y === boardSize-1,
    })
    const stoneClassNames = classnames('stone', {
      blank: color !== 1 && color !== 2,
      black: color === 1,
      white: color === 2,
      nextblack: nextMoveColor === 1,
      nextwhite: nextMoveColor === 2,
    })
    return (
      <div className={squareClassNames}>
        <div className="horiz-grid" />
        <div className="vert-grid" />
        <div className={stoneClassNames} />
      </div>
    );
  }
}

export default BoardSquare;



